/*=======  COMMON CSS  =======*/
@import url("https://fonts.googleapis.com/css2?family=Archivo:ital,wght@0,400;0,500;0,600;0,700;1,400;1,700&amp;family=Old+Standard+TT:ital,wght@0,400;0,700;1,400&amp;family=Roboto:wght@400;500;700&amp;display=swap");

@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap');

body {
    font-family: "Rubik", serif;
    font-weight: normal;
    font-style: normal;
    color: #777;
    overflow-x: hidden;
    font-size: 16px;
    line-height: 1.6;
    background-color: #fff;
}


:root {
    --bs-theme: #DE1819;
    --bs-gray-500: #9493A6;
    --bs-heading: #06003E;
    --bs-gray-bg: #E7E7E6;
    --bs-font: font-family: "Rubik", serif;
}

@media (max-width: 399px) {
    body {
        font-size: 14px;
    }
}

* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

img {
    max-width: 100%;
    -webkit-transition: all 0.4s ease-out 0s;
    transition: all 0.4s ease-out 0s;
}

a:focus,
input:focus,
textarea:focus,
button:focus,
.slick-initialized .slick-slide:focus,
.btn:focus {
    text-decoration: none;
    outline: none;
    -webkit-box-shadow: none;
    box-shadow: none;
}

a:focus,
a:hover {
    text-decoration: none;
}

i,
span,
a {
    text-decoration: none;
    display: inline-block;
}

h1,
h2,
h3,
h4,
h5,


h1,
h1 a,
h2,
h2 a,
h3,
h3 a,
h4,
h4 a,
h5,
h5 a,
h6,
h6 a {
    color: #222;
}

h1 {
    font-size: 48px;
}

h2 {
    font-size: 36px;
}

h3 {
    font-size: 28px;
}

h4 {
    font-size: 22px;
}

h5 {
    font-size: 18px;
}

h6 {
    font-size: 16px;
}

ul,
ol {
    margin: 0px;
    padding: 0px;
    list-style-type: none;
}

p {
    margin: 0px;
}

a {
    -webkit-transition: all 0.3s ease-out 0s;
    transition: all 0.3s ease-out 0s;
}

a,
a:hover {
    color: var(--bs-theme);
}

input,
select,
textarea,
select {
    width: 100%;
    height: 50px;
    background-color: #ffffff;
    padding: 0 20px;
    font-size: 14px;
    border: solid 1px #eee;
    border-radius: 5px !important;
}

textarea {
    height: 100px;
    padding-top: 25px;
}

::-webkit-input-placeholder,
input,
textarea,
select {
    font-family: "Roboto", sans-serif;
    color: #777777;
}

:-ms-input-placeholder,
input,
textarea,
select {
    font-family: "Roboto", sans-serif;
    color: #777777;
}

::-ms-input-placeholder,
input,
textarea,
select {
    font-family: "Roboto", sans-serif;
    color: #777777;
}

::placeholder,
input,
textarea,
select {
    font-family: "Roboto", sans-serif;
    color: #777777;
}

.nice-select {
    width: 100%;
    background-color: transparent;
    border-radius: 0;
    position: relative;
    border: none;
    height: 70px;
    padding-left: 50px;
    line-height: 70px;
}

.nice-select .list {
    width: 100%;
    border-radius: 0;
}

.nice-select .list li {
    line-height: 35px;
    color: #777;
}

.nice-select .list li:hover,
.nice-select .list li.option:hover {
    background-color: var(--bs-theme);
    color: #fff;
}

.nice-select::after {
    display: none;
}

.nice-select::before {
    content: '\f063';
    font-family: 'Font Awesome 5 Pro';
    font-weight: 300;
    position: absolute;
    left: 30px;
    top: 50%;
    color: var(--bs-theme);
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}

.input-group input,
.input-group select,
.input-group textarea,
.input-group select {
    padding-left: 50px;
}

.input-group label {
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 10px;
    color: #303030;
    display: block;
}

.input-group .icon {
    position: absolute;
    left: 20px;
    bottom: 25px;
    color: var(--bs-theme);
    -webkit-transform: translateY(50%);
    transform: translateY(50%);
}

.input-group.textarea .icon {
    top: 54px;
    bottom: auto;
    -webkit-transform: translateY(0);
    transform: translateY(0);
}

.input-group .nice-select {
    padding-left: 80px;
}

.input-group .nice-select::before {
    left: 40px;
}

.input-group.input-group-two input,
.input-group.input-group-two select,
.input-group.input-group-two textarea,
.input-group.input-group-two select {
    height: 60px;
    padding: 0px 50px 0 30px;
    background-color: #fff;
    color: #777;
}

.input-group.input-group-two ::-webkit-input-placeholder {
    opacity: 1;
    color: #777;
}

.input-group.input-group-two :-ms-input-placeholder {
    opacity: 1;
    color: #777;
}

.input-group.input-group-two ::-ms-input-placeholder {
    opacity: 1;
    color: #777;
}

.input-group.input-group-two ::placeholder {
    opacity: 1;
    color: #777;
}

.input-group.input-group-two .icon {
    left: auto;
    bottom: 30px;
    right: 30px;
}

.input-group.input-group-two .nice-select {
    background-color: #fff;
    padding: 0 50px 0 20px;
    height: 50px;
    line-height: 50px;
    border-radius: 5px !important;
    border: solid 1px #ededed;

}

.input-group.input-group-two input {
    background-color: #fff;
    padding: 0 20px 0 20px;
    height: 50px;
    line-height: 50px;
    border-radius: 5px !important;
    border-color: #ededed;
    font-size: 14px;
    width: 100%;
}

.input-group.input-group-two button {
    padding: 0 20px;
    height: 50px;
    width: 100%;
}

.input-group.input-group-two .nice-select::before {
    left: auto;
    right: 20px;
}

